import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CForm,
  CButton,
  CFormLabel,
  CFormInput,
  CFormFeedback,
  CButtonGroup,
  CFormSelect,
} from "@coreui/react";

import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";

import { useHistory, useParams } from "react-router-dom";

import Message from "../../components/Message";
import { Authentification } from "../../components/index";

import { addRestaurant, getRestaurants } from "../../provider/restaurantFetch";
import { getUsers } from "../../provider/userFetch";

const schemaUser = yup.object().shape({
  name: yup.string().required("name is required"),
  description: yup.string().required("description is required"),
  user: yup.string().required("user is required"),
});

const AjoutRestaurant = () => {
  /** State */
  const [message, setMessage] = useState(null);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState(false);
  const [commercants, setCommercants] = useState([]);
  const [errorCommercants, setErrorCommercants] = useState(false);

  /** Hook Route */
  let history = useHistory();
  let { id } = useParams();

  /** Form Hook */
  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting, isValid },
    resetField,
    setValue,
  } = useForm({
    resolver: yupResolver(schemaUser),
    mode: "onTouched",
  });

  /** Run once, when the component mounts */
  useEffect(() => {
    console.log("Run once, when the component mounts");

    async function fetchCommercant() {

      const respCommercant = await getUsers("commercant");

      respCommercant?.status
        ? setCommercants([...respCommercant?.data])
        : setErrorCommercants(respCommercant?.error);

    }
    fetchCommercant();

    if (id) {
      async function fetch() {
        const resp = await getRestaurants(id);

        if (resp?.status) {
          setValue("name", resp?.data.name);
          setValue("description", resp?.data.description);
          setValue("user", resp?.data.user._id);
        } else {
          setMessage(resp?.error);
        }
      }

      fetch();
    }
  }, [id, setValue]);

  const onSubmit = async (data) => {
    let resp = null;

    if (isValid) {
      /** reste */
      setSuccess(false);
      setError(false);
      resp = await addRestaurant(data, id);
    }

    if (resp?.status) {
      /** Reset */
      resetField("name");
      resetField("description");
      resetField("user");

      setSuccess(true);
    } else {
      setError(true);
      setMessage(resp?.error);
    }
  };

  const back = () => {
    history.push(`/restaurants`);
  };

  return (
    <CRow>
      <Authentification />
      {error && <Message> {message}</Message>}
      {success && <Message> {`Success Ajout ou Edite`}</Message>}
      <CCol xs={12}>
        <CCard className="mb-4">
          <CCardHeader>
            <CRow>
              <CCol sm={5}>
                <strong>Ajout Restaurant</strong>
              </CCol>
              <CCol sm={7}>
                <CButtonGroup className="float-end me-3">
                  <CButton
                    color="outline-secondary"
                    className="mx-0"
                    onClick={() => back()}
                  >
                    Retour
                  </CButton>
                </CButtonGroup>
              </CCol>
            </CRow>
          </CCardHeader>
          <CCardBody>
            <CForm
              className={`row g-3 needs-validation ${
                isValid && "was-validated"
              }`}
              onSubmit={handleSubmit(onSubmit)}
            >
              <CCol md={6}>
                <CFormLabel htmlFor="name">Name</CFormLabel>
                <CFormInput
                  type="text"
                  id="name"
                  required
                  {...register("name")}
                />
                <CFormFeedback invalid>{errors.name?.message}</CFormFeedback>
              </CCol>
              <CCol md={6}>
                <CFormLabel htmlFor="description">Description</CFormLabel>
                <CFormInput
                  type="text"
                  id="description"
                  required
                  {...register("description")}
                />
                <CFormFeedback invalid>
                  {errors.description?.message}
                </CFormFeedback>
              </CCol>
              <CCol md={6}>
                <CFormLabel htmlFor="user">Commercant</CFormLabel>
                {errorCommercants ? (
                  <Message> {errorCommercants}</Message>
                ) : (
                  <>
                    <CFormSelect id="user" required {...register("user")}>
                      <option disabled>Choose...</option>
                      {commercants.length &&
                        commercants.map((c) => (
                          <option key={c._id} value={c._id}>
                            {c.firstName} {c.lastName}
                          </option>
                        ))}
                    </CFormSelect>
                    <CFormFeedback invalid>
                      {errors.user?.message}
                    </CFormFeedback>
                  </>
                )}
              </CCol>
              <CCol xs={12}>
                <CButton
                  color="primary"
                  type="submit"
                  className={isSubmitting ? "disabled" : " "}
                >
                  Submit
                </CButton>
              </CCol>
            </CForm>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  );
};

export default AjoutRestaurant;
